.container {
    max-width: 100%;
    width: 100%;
    height: 100%;
    margin: 0 auto
}

.container.usercenter, .container.details {
    background: #f3f3f3 url(../img/page-bg.jpg) center bottom no-repeat;
    background-size: 100% auto
}

.container.indexBox {
    padding-top: 50px
}

.container.indexBox .logo {
    width: 60%;
    margin: 0 auto
}

.container.indexBox .logo img {
    width: 100%
}

.container.indexBox .slogan {
    width: 80%;
    margin: 0 auto;
    margin-top: 42px;
    margin-bottom: 10px
}

.container.indexBox .slogan img {
    width: 100%
}

.container.indexBox .slogan-sub {
    font-size: 12px;
    text-align: left;
    color: #fff;
    padding: 10px 30px;
    text-shadow: 0px 1px 1px #555;
    background: #3333338f;
}

.container.indexBox .linklist {
    position: absolute;
    bottom: 90px;
    left: 10px;
    right: 10px;
}

.container.indexBox .linklist ul {
    box-sizing: border-box;
    /* display: flex; */
}

.container.indexBox .linklist ul li {
    flex: 1;
    width: 33%;
    display: block;
    float: left;
    margin-top: 14px;
}

.container.indexBox .linklist ul li i.icon {
    display: block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 30px;
    color: #3961b0;
    background-color: #fff;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 5px
}

.container.indexBox .linklist ul li span {
    display: block;
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 16px
}

.container.indexBox .copyright {
    position: absolute;
    bottom: 20px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    left: 0;
    right: 0;
}

.container.usercenter .slide {
    width: 100%;
    height: 130px
}

.container.usercenter .slide .dot {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 10px;
    z-index: 5;
    font-size: 0;
    text-align: center;
    margin: 0 auto
}

.container.usercenter .slide .dot span {
    display: inline-block;
    width: 15px;
    height: 4px;
    margin-left: 4px;
    border-radius: 20px;
    background-color: #fff
}

.container.usercenter .slide .dot span.cur {
    background-color: #ff934b;
    border: none
}

.container.usercenter .title {
    color: #fff;
    font-size: 14px;
    display: flex;
    padding: 10px 10px;
    background-color: #3961b0
}

.container.usercenter .title a {
    color: #fff
}

.container.usercenter .title a:link {
    color: #fff
}

.container.usercenter .title .icon, .container.usercenter .title .more {
    width: 30px;
    text-align: center
}

.container.usercenter .title .list {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1
}

.container.leavedetails {
    overflow-x: hidden;
    overflow-y: scroll
}

.container.leavedetails .leaveinfo {
    padding: 15px;
    background-color: #fff;
    position: relative
}

.container.leavedetails .leaveinfo .info {
    border-radius: 8px;
    background-color: #264b94;
    padding: 20px 20px;
    display: flex
}

.container.leavedetails .leaveinfo .info .avatar {
    width: 60px;
    height: 60px;
    border: 1px solid #e0e0e0;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    color: #fff;
}

.container.leavedetails .leaveinfo .info .avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: #fff 2px solid
}

.container.leavedetails .leaveinfo .info .name {
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container.leavedetails .leaveinfo .info .name h2 {
    margin: 0;
    color: #fff;
    margin-bottom: 5px
}

.container.leavedetails .leaveinfo .info .name span {
    color: #fff
}

.container.leavedetails .leaveinfo.pass .info .name span {
    color: #4bd05f
}

.container.leavedetails .leaveinfo.pass:after {
    position: absolute;
    right: 40px;
    top: 40px;
    width: 90px;
    height: 90px;
    content: "";
    background: url("../img/status-pass.png") center center no-repeat;
    background-size: 100% 100%
}

.container.leavedetails .leaveinfo.notpass .info .name span {
    color: #ff4646
}

.container.leavedetails .leaveinfo.notpass:after {
    position: absolute;
    right: 40px;
    top: 40px;
    width: 90px;
    height: 90px;
    content: "";
    background: center center no-repeat;
    background-size: 100% 100%
}

.container.leavedetails .leaveinfo .infodetails {
    padding: 10px 10px;
    position: relative
}

.container.leavedetails .leaveinfo .infodetails .box {
    display: flex;
    justify-content: flex-start;
    margin: 15px 0
}

.container.leavedetails .leaveinfo .infodetails .box .item {
    flex: 1
}

.container.leavedetails .leaveinfo .infodetails .box .item span {
    display: block;
    font-size: 17px;
    font-weight: 500;
    color: #3961b0
}

.container.leavedetails .leaveinfo .infodetails .box .item span.title {
    color: #666;
    font-size: 14px;
    font-weight: normal
}

.container.leavedetails .leaveinfo .infodetails:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #577ecb
}

.container.leavedetails .leaveinfo .explain {
    border-top: #f3f3f3 1px solid;
    padding: 15px 10px 10px 10px
}

.container.leavedetails .leaveinfo .explain .box {
    margin: 0 0 10px 0;
    font-size: 14px;
    display: flex
}

.container.leavedetails .leaveinfo .explain .box .title {
    width: 80px;
    color: #666
}

.container.leavedetails .leaveinfo .explain .box .infocontent {
    margin: 0;
    flex: 1;
    font-weight: 500;
    color: #3961b0
}

.container.leavedetails .flow {
    margin: 15px 0;
    background-color: #fff;
    padding: 10px 16px;
    position: relative
}

.container.leavedetails .flow:before {
    content: "";
    width: 3px;
    position: absolute;
    left: 35px;
    top: 50px;
    bottom: 45px;
    background-color: #dddddd;
}

.container.leavedetails .flow > ul > li {
    display: flex;
    position: relative;
    padding: 25px 0
}

.container.leavedetails .flow > ul > li:last-child:after {
    display: none
}

.container.leavedetails .flow > ul > li:after {
    content: "";
    height: 1px;
    background-color: #f3f3f3;
    position: absolute;
    bottom: 0;
    left: 65px;
    right: 0px
}

.container.leavedetails .flow > ul > li .avatar {
    width: 50px;
    height: 50px;
    position: relative;
    background-color: #3961b0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 18px;
    color: #e0e0e0;
}

.container.leavedetails .flow > ul > li .avatar.pass:after {
    border: #fff 2px solid;
    position: absolute;
    right: -4px;
    bottom: -4px;
    content: "";
    width: 18px;
    height: 18px;
    background: #4bd05f url("../img/icon-pass.png") center center no-repeat;
    border-radius: 50%;
    background-size: 100% 100%
}

.container.leavedetails .flow > ul > li .avatar.notpass:after {
    border: #fff 2px solid;
    position: absolute;
    right: -4px;
    bottom: -4px;
    content: "";
    width: 18px;
    height: 18px;
    background: #ff4646 center center no-repeat;
    border-radius: 50%;
    background-size: 100% 100%
}

.container.leavedetails .flow > ul > li .avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: #fff 3px solid
}

.container.leavedetails .flow > ul > li .avatar.icon {
    width: 50px;
    height: 36px
}

.container.leavedetails .flow > ul > li .avatar .icon {
    color: #fff;
    display: block;
    width: 36px;
    height: 36px;
    line-height: 36px;
    border-radius: 50%;
    background-color: #3961b0;
    text-align: center;
    font-size: 20px;
    margin: 0 auto;
    border: #fff 3px solid;
    box-sizing: content-box
}

.container.leavedetails .flow > ul > li .pointer {
    width: 50px;
    height: 20px
}

.container.leavedetails .flow > ul > li .pointer .icon {
    border: #fff 3px solid;
    box-sizing: content-box;
    margin: 0px 13px;
    margin-top: 5px;
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #3961b0;
}

.container.leavedetails .flow > ul > li .content {
    padding-left: 15px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container.leavedetails .flow > ul > li .content.pointer .ttl {
    font-size: 14px;
    font-weight: normal;
    color: #979797
}

.container.leavedetails .flow > ul > li .content .ttl {
    font-size: 18px;
    font-weight: 500;
    color: #555;
    display: flex;
    justify-content: space-between
}

.container.leavedetails .flow > ul > li .content .ttl .time {
    color: #979797;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
}

.container.leavedetails .flow > ul > li .content .ttl .oper {
    padding-top: 10px
