* {
    font-size: 15px;
    font-weight: 200;
    font-family: "Microsoft YaHei", "iconfont", serif;
}

ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

html, body, .main {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #efefef;
}

.topinfo {
    height: 17%;
    background-color: #3961b0;
}

.topbar {
    height: 52px;
    background-color: #3961b0;
}

.topbar .top-head {
    height: 100%;
    border-bottom: 1px solid #3473D8;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    font-weight: 200;
    color: #eaeaea;
    padding: 0 15px;
}

.topinfo .top-head {
    height: 100%;
    border-bottom: 1px solid #3961b0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 600;
    color: #eaeaea;
}

.topinfo .bottom-head {
    height: 100%;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
}

.topinfo .bottom-head .l {
    display: flex;
    flex: 1;
}

.topinfo .bottom-head .l .pic-box {
    flex: .4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.topinfo .bottom-head .l .pic-box .pic {
    width: 60px;
    height: 60px;
    background-color: #fff;
    border-radius: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.topinfo .bottom-head .l .pic-box .pic-inner {
    width: 50px;
    height: 50px;
    background-color: #3961b0;
    border-radius: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.topinfo .bottom-head .l .info {
    flex: 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
}

.topinfo .bottom-head .l .info .info-name {
    font-size: 16px;
    line-height: 2;
    color: #fff;
    font-weight: 500;
}

.topinfo .bottom-head .l .info .info-desc {
    font-size: 12px;
    color: #f7f7f7;
}

.topinfo .bottom-head .r {
    flex: .3;
    background-color: rebeccapurple;
}

.bodyer {
    min-height: 83%;
}

.bodyer .box-parent {
    margin: 10px 0;
}

.bodyer .box-parent .boxt {
    padding: 5px 0px 0px 0px;
    /* margin-bottom: 2px; */
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    /* height: 13px; */
    color: #0e0e0e;
    font-size: 16px;
    line-height: 36px;
    font-weight: 600;
}

.bodyer .box-parent .boxt::before {
    content: ' ';
    display: inline-block;
    background-color: #3473D8;
    width: 4px;
    height: 12px;
    margin-right: 5px;
    /* margin-top: 0px; */
    /* position: absolute; */
}

.bodyer .box-parent .box {
    display: flex;
    min-height: 100px;
    background-color: #fff;
    /* margin: 10px 0;
    justify-content: space-around; */
    flex-wrap: wrap;
}

.bodyer .box-parent .box > li {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    padding: 5px;
    box-sizing: border-box;
}

.bodyer .box > li i {
    margin: 5px 0;
    padding: 8px 10px;
    border-radius: 10px;
    color: #fff;
}

.bodyer .box > li span {
    margin: 0px 0 10px 0;
    color: #696969;
    font-size: 12px;
}

.footer {
    font-size: 12px;
    text-align: center;
    color: #868686;
}

.footer :first-child {
