.not-info {
    display: flex;
    height: 60%;
    justify-content: center;
    align-items: center;
}

.not-info .not-info-box {
    flex: 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.not-info-box-icon {
    margin-bottom: 8px;
}

.not-info-box-icon > i {
    color: #8a8a8a;
    font-size: 50px;
}

.not-info-box-msg {
    font-size: 12px;
    color: #8a8a8a;
}

.icon-back {
    font-size: 14px;
}

::-webkit-scrollbar {
    width: 0px;
    background: #fbe1c0;
}

::-webkit-scrollbar-thumb {
    background: #c56b6a;
}

.box {
    display: block;
    border-bottom: 1px solid #f5f5f5;
    padding: 5px 0px;
    min-height: 26px;
}

.box-top {
    text-align: center;
    margin: 0 auto;
    width: 90%;
}

.box-top .box-top-title {
    width: 100%;
    max-width: 300px;
    font-weight: 600;
    font-size: 16px;
    padding: 10px 0px;
}

.box-fbsj {
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    color: #adafaf;
    padding: 20px 0 25px;
}

.binfo-box-row {
    display: flex;
    justify-content: right;
    padding: 5px 0px;
}

.binfo-box-row > label {
    color: #666;
    width: 75px;
    display: inline-table;
    text-align-last: justify;
    text-align: justify;
}

.binfo-box-row > span {
    color: #444;
    width: 80%;
    word-wrap: break-word;
    word-break: break-all;
    display: table-cell;
}

    	